import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { UserService } from "../../service/user.service";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";

@Component({
  selector: "app-add-user",
  templateUrl: "./add-user.component.html",
  styleUrls: ["./add-user.component.scss"],
  providers: [SweetalertComponent],
})
export class AddUserComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];
  public customerFrom: FormGroup;
  public submitted = false;
  public editable = false;
  public id;
  selectedCountry: string = null;
  selectedCity: string = '';
  country = ['pakistan', 'india', 'usa', 'uk', 'canada'];
  cityData: { [key: string]: string[] } = {
    pakistan: ['Karachi', 'Lahore', 'Islamabad', 'Rawalpindi'],
    india: ['Mumbai', 'Delhi', 'Bangalore', 'Hyderabad'],
    usa: ['New York', 'Los Angeles', 'Chicago', 'Houston'],
    uk: ['London', 'Manchester', 'Birmingham', 'Glasgow'],
    canada: ['Toronto', 'Vancouver', 'Montreal', 'Calgary']
  };
customerType = ['Stitching','Wholesale Fabric Customer','Wholesale Fabric Supplier']

  constructor(
    private _sw: SweetalertComponent,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _serviceData: UserService,
    private _router: Router
  ) {}

  updateCityDropdown(): void {
    this.selectedCity = ''; // Clear selected city when country changes
  }

  navigateToCustomer() {
    this._router.navigate(['/customer']);
  }


  onSubmit(formData) {
    this.submitted = true;
    if (this.customerFrom.invalid) {
      return;
    }

    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._serviceData.create(formData).subscribe(
      (res) => {
        this._sw.fire("success", "Customer", `Record Successfully Add..`);
        this.getAllDetail();
        this.submitted = false;
        this.customerFrom.reset();
      },
      (err) => {
        this._sw.fire("error", "Customer", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._serviceData.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire("success", "Customer", `Record Successfully Update..`);
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/customer"]);
      },
      (err) => {
        this._sw.fire("error", "Customer", err.error.message);
      }
    );
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");

    if (this.id > 0) {
      this.getDetailById();
    }
    this.getAllDetail();
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Customer",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Customer",
            isLink: true,
            link: "/",
          },
          {
            name: "Information",
            isLink: false,
          },
        ],
      },
    };
  }
  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      }
    });
  }

  get f() {
    return this.customerFrom.controls;
  }

  getDetailById() {
    this._serviceData.getDetailById({ id: this.id }).subscribe((res) => {
      this.editable = true;
      this.customerFrom.patchValue({
        full_name: res.data.full_name,
        email: res.data.email,
        phone: res.data.phone,
        country:res.data.country,
        city:res.data.city,
        alternateNumber:res.data.alternateNumber,
        customerType:res.data.customerType,
        address:res.data.address
      });
    });
  }

  makeReactiveForm() {
    this.customerFrom = this.fb.group({
      full_name: ["", [Validators.required]],
      email: [""],
      phone: ["", [Validators.required]],
      alternateNumber: [""],
      country: [null, [Validators.required]],
      city: [null, [Validators.required]],
      customerType: [null, [Validators.required]],
      address: [""],
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }
}
